.site-list__item a {
  color: @basic-black;
  text-decoration: none;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .site-list__item a {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    text-transform: uppercase;
  }
}

@media (min-width: 1440px) {
  .site-list__item a {
    line-height: 29px;
  }
}

.site-list {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .site-list {
    display: flex;
    justify-content: space-between;
  }

  .site-list__item:not(:nth-child(3)) {
    margin-right: 33px;
  }

  .site-list__item:nth-child(1) {
    margin-right: 7.5%;
  }

  .site-list__item:nth-child(2) {
    margin-right: 7.9%;
  }
}
